body {
  margin: 0;
 font-family: Verdana, Geneva, Tahoma, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(238,174,202);
  background-image: linear-gradient(to right, #fffbd5, #b20a2c);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #030303;
  background-image: linear-gradient(to right, #fffbd5, #b20a2c);
}

.header a,
.header button {
  font-size: 1.2rem;
  display: inline-block;
  /* background-color: #b0c4de; */
  border: 2px solid rgb(24, 23, 23);
  margin: 2rem;
  padding: 1rem;
  text-decoration: none;
  color: black;
  cursor: pointer;
  border-radius: 5rem;
}

.student-search{
  font-size: 10px;
  background-color: #faf0e6;
  border: 0.5px solid rgb(2, 1, 1);
  flex-direction: row;
  width: 400px;
  margin: 10px;
  padding-bottom: 0rem;
  align-items: center;
  border-radius: .5rem;
  text-align: justify;
  margin-left: 50%;
  position: absolute;


}

form {
  background-color: #faf0e6;
  border: 0.5px solid rgb(2, 1, 1);
  display: flex;
  flex-direction: column;
  width: 300px;
  margin: 0 auto;
  padding: 0 1rem 1rem;
  border-radius: .5rem;
}


form input {
  margin: 0.25rem 0;
  display: block;
  box-sizing: border-box;
  width: 90%;
  height: 2.4375rem;
  margin-top:3%;
  margin-left: 4%;
  padding: .5rem;
  /* border: 1px solid #e2e2e2; */
  border-radius: .5rem;
  background-color: #adbffa;
  box-shadow: inset 0 1px 2px rgb(10 10 10 / 10%);
  font-family: inherit;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #0a0a0a;
  transition: box-shadow .5s,border-color .25s ease-in-out;
  background-color: rgb(255, 255, 255);
  -webkit-appearance: none;
  appearance: none;
  
}

form button[type="submit"] {
  width: 15%;
  height: 10%;
  font-family: "SF Pro Text", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 1.75;
  color: white;
  border: none; /* Remove the default border */
  border-radius: .5rem;
  background: linear-gradient(#090a0a, #0b0c0c);
  box-shadow: 
      inset 0 1px 0 rgba(255,255,255,0.3),
      0 1px 2px rgba(0,0,0,0.35),
      inset 0 3px 2px rgba(255,255,255,0.2),
      inset 0 -3px 2px rgba(0,0,0,0.1);

  cursor: pointer;
  border-radius: 25px;
  padding: 9px 20px;
  font-stretch: normal;
  font-style:normal;
  white-space: nowrap;
  font-weight: 600;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  margin-top: 2%;
  width: 40%;
}

.students-nav-bar{
  font-size: 1rem;
  background-color: #f8f8f8;
  border: 1px solid rgb(24, 23, 23);
  margin-left: -20px;
  padding: 1rem;
